/*--------------------adjustment----------------------*/
.main-wrapper {
  display: flex;
  flex-direction: column;
}

.main-wrapper .main-content {
  flex: 1 1 auto;
}

.main-wrapper .footer {
  flex: 0 0 auto;
}

.hidden {
  display: none;
}

.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  padding-bottom: 52.25%;
}

.ibg img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

._prod-img {
  position: relative;
  padding-bottom: 80%;
}

._prod-img img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

._fit {
  padding-bottom: 80%;
  position: relative;
}

._fit ._fit-img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  object-fit: cover;
  object-position: center center;
}

@supports (backdrop-filter: blur(10px)) or (--webkit-backdrop-filter: blur(10px)) {
  .bd-filter-fallback {
    background-color: rgba(255, 255, 255, 0.72);
    backdrop-filter: blur(31px);
  }
}

/*--------------------END adjustment----------------------*/
/*--------------------ui----------------------*/
/*--------------------END ui----------------------*/
.header .sidebar-menu {
  display: flex;
}

.header .sidebar-menu .dl-submenu {
  opacity: 0;
  visibility: hidden;
}

/*--------------------search-results-page----------------------*/
.page-search-resuls .title-with-dd {
  padding-top: 40px;
}

.title-with-dd {
  padding-top: 40px;
  font-size: 42px;
  margin-bottom: 45px;
  font-size: 42px;
  line-height: calc(50 / 42);
  font-weight: 500;
}

@media (max-width: 1170px) {
  .title-with-dd {
    font-size: calc(31px + 11 * ((100vw - 320px) / 850));
  }
}

@media (max-width: 1170px) {
  .title-with-dd {
    margin-bottom: calc(31px + 14 * ((100vw - 320px) / 850));
  }
}

.title-with-dd span {
  margin-right: 16px;
}

.title-with-dd a {
  position: relative;
  color: #FC6733;
  border-bottom: 1px dashed #FC6733;
}

.filter-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  padding-bottom: 24px;
  border-bottom: 1px solid rgba(34, 34, 34, 0.05);
}

.filter-tags:not(:last-child) {
  margin-bottom: 24px;
}

.filter-tag {
  display: flex;
  align-items: center;
  height: 40px;
  padding: 10px 15.44px;
  background: #FFFFFF;
  border-radius: 20px;
}

.filter-tag.active {
  background-color: #FC6733;
  color: #fff;
}

.filter-tag.active .icon-close {
  display: block;
}

.filter-tag .icon-close {
  display: none;
  color: #fff;
}

.filter-tag__icon {
  font-size: 17px;
}

.filter-tag__icon.icon-type {
  font-size: 24px;
}

.filter-tag__name {
  margin-left: 8.6px;
  margin-right: 9.73px;
  font-size: 14px;
}

.results-filter {
  display: flex;
  justify-content: space-between;
}

.results-filter__items {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.results-filter__item {
  display: flex;
  position: relative;
  height: 48px;
  align-items: center;
  padding: 8px 20px;
  background: #FFFFFF;
  border-radius: 44px;
  cursor: pointer;
}

.results-filter__item--services .item-results-filter__dd {
  width: 421px;
}

.results-filter__item .form_inp {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
}

.item-results-filter__name {
  margin-right: 12px;
  font-size: 14px;
}

.item-results-filter__dd {
  position: absolute;
  top: 100%;
  left: 0;
  width: 368px;
  padding: 16px;
  background: #FFFFFF;
  box-shadow: 0px 4px 24px rgba(34, 34, 34, 0.25);
  border-radius: 12px;
  opacity: 0;
  visibility: hidden;
  display: none;
}

.item-results-filter__dd .place_service-item.disabled {
  background-color: rgba(34, 34, 34, 0.1);
  color: #fff;
}

.item-results-filter__dd .place_service-item.disabled .place_service-item-icon {
  color: #fff;
}

.item-results-filter__dd .place_service-item .place_service-item-icon {
  color: #FC6733;
}

.results-filter__map-open {
  font-size: 14px;
}

.results-filter__map-open .icon-place_contacts-item2 {
  margin-right: 10px;
}

.results-filter__map-open .icon-place_contacts-item2:before {
  font-size: 19px;
  color: #222222;
}

/*--------------------END search-results-page----------------------*/
